import "./App.css";
import React, { useState, useEffect } from "react";
import { PublicClientApplication } from "@azure/msal-browser";
import Header from "./Header";
import Main from "./Main";
import List from "./List";
import config from "./config";

const App = () => {
  let loginTimeoutID = null;
  const [authenticated, setAuthenticated] = useState(false);
  const [mobileMode, setMobileMode] = useState(false);
  const [user, setUser] = useState(null);
  const [idToken, setIdToken] = useState(null);
  const [username, setUsername] = useState(null);
  const [error, setError] = useState(null);
  const [shortURL, setShortURL] = useState(null);

  useEffect(() => {
    if (window.innerWidth < 768) {
      setMobileMode(true);
    } else {
      setMobileMode(false);
    }
    const windowResizeEventListener = window.addEventListener("resize", () => {
      if (window.innerWidth < 768) {
        setMobileMode(true);
      } else {
        setMobileMode(false);
      }
    });
    return () => {
      window.removeEventListener("resize", windowResizeEventListener);
    };
  }, []);

  useEffect(() => {
    let timeoutID;
    if (error) {
      timeoutID = setTimeout(() => {
        setError(null);
      }, 2000);
    }
    return () => {
      clearTimeout(timeoutID);
    };
  }, [error]);

  const publicClientApplication = new PublicClientApplication({
    auth: {
      clientId: config.appId,
      redirectUri: config.redirectUri,
      authority: config.authority,
    },
    cache: {
      cacheLocation: "localStorage",
      storeAuthStateInCookie: true,
    },
  });

  const login = () => {
    try {
      publicClientApplication
        .loginPopup({
          scopes: config.scopes,
          prompt: "select_account",
        })
        .then((res) => {
          loginTimeoutID = setTimeout(logout, 3600000);
          setUser(res.account.name);
          setIdToken(res.idToken);
          setUsername(res.account.username);
          setAuthenticated(true);
        });
    } catch (err) {
      console.log(err);
      setError("Something went wrong! :(");
    }
  };

  const logout = () => {
    try {
      publicClientApplication.logoutPopup().then((res) => {
        clearTimeout(loginTimeoutID);
        setUser(null);
        setAuthenticated(false);
      });
    } catch (err) {
      console.log(err);
      setError("Something went wrong! :(");
    }
  };

  return (
    <div>
      {error ? (
        <div className="alert">
          <strong>{error}</strong>
        </div>
      ) : null}
      <div className={error ? "blur" : ""}>
        <Header
          user={user}
          authenticated={authenticated}
          login={login}
          logout={logout}
        />
        <Main
          authenticated={authenticated}
          idToken={idToken}
          username={username}
          setError={setError}
          mobileMode={mobileMode}
          shortURL={shortURL}
          setShortURL={setShortURL}
        />
        {authenticated ? (
          <List
            idToken={idToken}
            username={username}
            mobileMode={mobileMode}
            shortURL={shortURL}
          />
        ) : null}
      </div>
    </div>
  );
};

export default App;
